.filters{
    position: fixed;
    background-color: #343a40;
    color: white;
    padding: 20px;
    display: flex;
    flex-direction: column;
    margin: 10px;
    
 
    border: 10px solid rgb(168, 168, 105);
   

}
.filters > span {
    padding-bottom: 20px;
}
.title{
    font-size: 2.4rem;
}
.cartItem{
    display: flex;
    justify-content: space-between;
    margin: 0 20px;
    margin-bottom: 20px;
}
.cartItemImg{
    border-radius: 50%;
    width: 50px;
    height: 50px;
    object-fit: cover;
}
.cartItemDetail{
    display: flex;
    flex: 1;
    padding: 0 20px;
    flex-direction: column;
}


@media (max-width: 480px) {
    .filters{
        flex-direction: row;
        margin-bottom: 100px;
    
    }
  }
  
